.gallery {
    padding: 50px 20px;
    background-color: #fff;
    text-align: center;
  }
  
  .gallery h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
  }
  
  
  .gallery-images img {
    /* width: calc(25% - 20px); */
    max-width: 1000px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  @media (max-width: 1012px) {
    .gallery-images img {
      width: calc(80% - 20px);
    }     
  }
  @media (max-width: 768px) {
    .gallery-images img {
      width: calc(70% - 20px);
    }
  }
  
  @media (max-width: 480px) {
    .gallery-images img {
      width: 100%;
    }
  }