header {
    /* background-color: rgb(87, 41, 4); */
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
header .logo {
width:  6rem;
filter: invert();

}
  
  header nav {
    text-align: center;
  }
  
  header nav ul {
    list-style: none;
    padding: 0;
  }
  
  header nav ul li {
    display: inline-block;
    margin: 0 15px;
  }
  
  header nav ul li a {
    color: white;
    font-size: 18px;
    cursor: pointer;

  }
  
  header nav ul li a:hover {
    text-decoration: underline;
  }

