.shade{
  background-color: rgba(0, 0, 0, 0.726);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero {
    background: url("F:\learn react-js\Portfolio\model-file5\src\assets\images\pic\barber2.jpg") no-repeat center center /cover;
    height: 80vh;
    color: rgb(255, 255, 255);
    text-align: center;
    position: relative;
    margin-top: -180px;
    z-index: -1;
  }
  

  .hero-content h1 {
    font-size: 3em;
    margin-top: 30px;
    margin-bottom: 10px;

  }
  
  .hero-content p {
    font-size: 1.5em;
  }


  @media (max-width: 700px) 
  {
  
  .hero-content h1 {
    font-size: 2rem;
  }
  
  .hero-content p {
    font-size: 1.5em;
  }
  }