.services {
    padding: 50px 20px;
    background-color: #fff;
    text-align: center;
  }
  
  .services h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
  }
  
  .service-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .service-card {
    background-color: #ccc37183;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 300px;
    flex: 1;
    min-width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
  }
  
  .service-card:hover{
    background-color: #ccc371f1;
    transform: scale(1.1);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

  }

  .service-card h3 {
    margin-bottom: 10px;
  }
  
  .service-card p {
    color: #555;
  }