.team {
    padding: 50px 20px;
    background-color: #4e362a;
    /* background-color: #ccc371; */
    text-align: center;
  }
  
  .team h2 {
    color: #ffffff;
    font-size: 2.5em;
    margin-bottom: 20px;
  }
  
  .team-members {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .team-member {
    background-color: #ccc37183;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 300px;
    flex: 1;
    min-width: 200px;
    outline: 1px solid white;
  }
  
  .team-member img {
    width: 100%;
    border-radius: 50%;
    margin-bottom: 10px;
  }
  
  .team-member h3 {
    margin-bottom: 5px;
  }
  
  .team-member p {
    color: #555;
  }