footer {
    background-color: #4e362a;
    color: white;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0 20px;
  }
  
.smallContact form{
  background-color: rgba(0, 0, 0, 0.568);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25rem;
  height: 18rem;
  padding-top: 25px;
  border-radius: 10px;
  outline: white 2px solid;
}
.shade3{
  background: url('F:\learn react-js\Portfolio\model-file5\src\assets\images\pic\interior.webp') no-repeat center center/cover;
  border-radius: 10px;
  width: 25rem;
  height: 18rem;
  width: calc(25% -20px);

}

.smallContact form input, .smallContact form textarea{
  width: 90%;
  max-width: 500px;
  padding: 10px;
  margin: 10px 0;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.smallContact form button{
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #333;
  color: white;
  cursor: pointer;
}

.smallContact form button:hover {
  background-color: #555;
}

  .footer-section {
    flex: 1;
    min-width: 200px;
    margin: 10px;
  }
  
  .footer-section h2 {
    margin-bottom: 15px;
  }
  
  .footer-section ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-section ul li {
    margin: 10px 0;
  }
  
  .footer-section ul li a {
    color: white;
    text-decoration: none;
  }
  
  .footer-section ul li a:hover {
    text-decoration: underline;
  }
  
  .footer-bottom {
    margin-top: 20px;
  }
.about p{
  text-align: justify;
  margin-top: 20px;
  padding: 10px;
  line-height: 1.5;
}

.icon{
  width: 50px;
  padding: 5px;
  cursor: pointer;
}
.icon:hover{
  transform: scale(1.2);
}


  
  @media (max-width: 884px) {
  
  .footer-content{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
 .smallContact form{
   width: 300px; 
   border-radius: 10px ;
  }
  
  .shade3{
   border-radius: 10px ;
   width: 300px;
  }
  
}

  @media (max-width: 344px) {
  
 .smallContact form{
   width: 250px; 
  }
  
  .shade3{
    width: 250px;
    }
  }
  @media (max-width: 290px) {
  
 .smallContact form{
   width: 200px; 
  }
  
  .shade3{
    width: 200px;
    }
  }