.contact-form {
  padding: 50px 20px;
  text-align: center;
  background-color: #000000a8;
}

.shade2{
  background: url('F:\learn react-js\Portfolio\model-file5\src\assets\images\pic\interior.webp') no-repeat center center/cover;
  height: 100%;
  width: 100%;

}

  .contact-form h2 {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: white;
  }
  
  .contact-form form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    max-width: 500px;
    padding: 10px;
    margin: 10px 0;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  .contact-form button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    background-color: #333;
    color: white;
    cursor: pointer;
  }
  
  .contact-form button:hover {
    background-color: #555;
  }

  @media (max-width: 700px) 
  {
  
  .contact-form h2 {
    font-size: 2rem;
  }
}
  @media (max-width: 350px) 
  {
  
  .contact-form h2 {
    font-size: 1.5rem;
  }
}